import { request } from "./request";
import Vue from 'vue'

export function areaQYList() {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvDataAnalyseAction/areaQYList?areaId=" + sessionStorage.getItem('companyId'),
        method: 'POST'
    })
}
export function companyDZList(companyId) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvDataAnalyseAction/companyDZList?companyId=" + companyId,
        method: 'POST'
    })
}
export function analyse(pvStationId) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvDataAnalyseAction/analyseNBQ?pvStationId=" + pvStationId,
        method: 'POST'
    })
}
export function analyseNBQDetail(fromDate,pvEqmId,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvDataAnalyseAction/analyseNBQDetail?fromDate=" + fromDate + '&pvEqmId=' + pvEqmId + '&toDate=' + toDate,
        method: 'POST'
    })
}
