<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      :modal="false"
      width="80%"
    >
      <div style="display: flex; align-items: center; margin-bottom: 0.2rem">
        <div style="margin-right: 0.2rem">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="date1"
            type="date"
            placeholder=""
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="margin-right: 0.2rem; color: #00b4fe">至</div>
        <div>
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="date2"
            type="date"
            placeholder=""
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <button class="searchBth" @click="analyseQXYDetailFun()">查询</button>
      </div>
      <div class="content" v-if="tableData.length > 0">
        <div>
          <el-table :data="tableData" height="400" style="width: 100%">
            <template v-for="(item, index) in showList">
              <el-table-column
                :prop="item.key"
                :label="item.name"
                :width="item.width"
                :key="index"
              >
              </el-table-column>
            </template>
          </el-table>
        </div>
      </div>
      <!-- <div style="width: 100%; height: 5rem" ref="echart"></div> -->
    </el-dialog>
  </div>
</template>
<script>
import { analyseQXYDetail } from "@/network/inverter";
export default {
  data() {
    return {
      dialogVisible: false,
      arry: [],
      date1: "",
      date2: "",
      showList: [],
      tableData: [],
    };
  },
  watch: {},
  mounted() {},
  methods: {
    showDialogFun(id) {
      this.pvEqmId = id;
      this.date2 = this.date1 =
        this.$getDate().year +
        "-" +
        this.$getDate().month +
        "-" +
        this.$getDate().day;
      this.dialogVisible = true;
      this.analyseQXYDetailFun();
    },
    analyseQXYDetailFun() {
      analyseQXYDetail(this.pvEqmId, this.date1, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
            this.showList = [];
            let contentObj = content[0];
            let keys = Object.keys(contentObj);
            keys.forEach((item) => {
              let width = "150";
              if (item.length > 8) {
                width = "200";
              }
              this.showList.push({
                name: item,
                key: item,
                width: width,
              });
            });
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
