<template>
  <div class="inverter">
    <div class="search">
      <div>
        <span>企业</span>
        <el-select v-model="value" placeholder="" @change="changeCompany">
          <el-option
            v-for="item in options"
            :key="item.companyId"
            :label="item.companyName"
            :value="item.companyId"
          >
          </el-option>
        </el-select>
      </div>
      <div>
        <span>光伏电站</span>
        <el-select v-model="DZvalue" placeholder="">
          <el-option
            v-for="item in DZOptions"
            :key="item.pVStationId"
            :label="item.pVStationName"
            :value="item.pVStationId"
          >
          </el-option>
        </el-select>
      </div>
      <button class="searchBth" @click="analyseQXYFun()">查询</button>
    </div>
    <div class="bottom" v-show="showData">
      <div v-for="(item, index) in chartArry" :key="index">
        <p>{{ item.name }}</p>
        <p class="icon" @click="showDeatil(item.id)">
          <img style="width: 0.5rem" src="@/assets/inverter/qxy.png" alt="" />
        </p>
        <div class="data dataContent">
          <p><span>环温：</span>{{ item.value1 }}<span></span></p>
          <p><span>风向瞬时值：</span>{{ item.value2 }}<span></span></p>
          <p><span>环境湿度：</span>{{ item.value3 }}<span></span></p>
          <p><span>风速瞬时值：</span>{{ item.value4 }}<span></span></p>
        </div>
        <p class="state" v-if="item.state == '正常'">
          <img style="width: 0.26rem" src="@/assets/inverter/yes.png" alt="" />
          {{item.state}}运行
        </p>
        <p class="state" v-else style="background:#be2121">
          <img style="width: 0.26rem" src="@/assets/inverter/no.png" alt="" />
          {{ item.state }}
        </p>
      </div>
    </div>
    <weatherInstrumentDialog ref="dialog"></weatherInstrumentDialog>
  </div>
</template>
<script>
import { areaQYList, companyDZList, analyseQXY } from "@/network/inverter";
import weatherInstrumentDialog from "./components/weatherInstrument_dialog.vue";
export default {
  components: {
    weatherInstrumentDialog,
  },
  data() {
    return {
      showData: false,
      value: "",
      options: [],
      DZvalue: "",
      DZOptions: [],
      chartArry: [],
    };
  },
  mounted() {
    this.areaQYListFun();
  },
  methods: {
    showDeatil(id) {
      this.$refs.dialog.showDialogFun(id);
    },
    /**
     * 切换企业
     * */
    changeCompany(val) {
      this.companyDZListFun(val);
    },
    /**
     * 获取企业列表
     * **/
    areaQYListFun() {
      areaQYList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.options = content;
        })
        .catch((err) => {});
    },
    /**
     * 获取电站列表
     * **/
    companyDZListFun(val) {
      companyDZList(val)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          this.DZOptions = content;
        })
        .catch((err) => {});
    },
    analyseQXYFun() {
      analyseQXY(this.DZvalue)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.showData = true;
            this.chartArry = [];
            content.forEach((item) => {
              this.chartArry.push({
                name: item['气象仪名称'],
                id: item['气象仪编号'],
                state:item['状态'],
                value1:Number(item['环温']).toFixed(2),
                value2: Number(item['风向瞬时值']).toFixed(2),
                value3: Number(item['环境湿度']).toFixed(2),
                value4: Number(item['风速瞬时值']).toFixed(2),
              });
            });
          }
        })
        .catch((err) => {});
    },
  },
};
</script>
<style scoped lang="less">
.inverter {
  background-image: url(@/assets/BG2.png);
  background-size: 100% 100%;
  display: flex;
  flex-direction: column;
  padding: 0.2rem;
  box-sizing: border-box;
  background-color: rgba(0, 15, 39, 1);
  width: 100%;
}

.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
}

.box_title {
  color: #4abbff;
  line-height: 0.55rem;
  letter-spacing: 0.02rem;
  position: relative;
  background: url("@/assets/powerStation/titlePng.png");
  background-size: 100% 100%;
  text-align: center;
  font-size: 0.24rem;
  font-weight: bold;
}
.box_title::after {
  display: inline-block;
  width: 0.75rem;
  height: 0.17rem;
  content: "";
  background: url("@/assets/powerStation/title-left.png");
  position: absolute;
  left: 0.6rem;
  top: 50%;
  transform: translateY(-50%);
  background-size: 100% 100%;
}
.box_title::before {
  display: inline-block;
  width: 0.75rem;
  height: 0.17rem;
  content: "";
  background: url("@/assets/powerStation/title-right.png");
  position: absolute;
  right: 0.6rem;
  top: 50%;
  transform: translateY(-50%);
  background-size: 100% 100%;
}
.box-content {
  padding: 0 0.25rem 0;
  box-sizing: border-box;
  position: relative;
}
.search {
  display: flex;
  > div {
    margin-right: 0.48rem;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
  }
}
/deep/ .el-input__inner {
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
  color: #fff !important;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.47rem;
  border: none;
  cursor: pointer;
}
.center {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 0.16rem;
  > div {
    display: flex;
    align-items: center;
    width: 32.8%;
    border: 1px solid rgba(34, 175, 237, 0.5);
    background: rgba(0, 55, 146, 0.2);
    border-radius: 0.05rem;
    padding: 0.13rem 0.26rem 0.13rem 0.14rem;
    box-sizing: border-box;
    font-size: 0.18rem;
    margin-bottom: 0.15rem;
  }
  .data {
    color: #00b4fe;
    font-weight: bold;
    font-size: 0.3rem;
  }
}
.dataContent span{
  color: #fff;
  font-size: 0.16rem;
}
.center .icon {
  width: 0.63rem;
  height: 0.63rem;
  background: url("@/assets/inverter/icon.png");
  background-size: 100% 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bottom {
  background: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
  flex-grow: 1;
  padding: 0.3rem 0.23rem;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  overflow: scroll;
  margin-top: .2rem;
  > div {
    width: calc(100% / 4 - 0.18rem);
    background: rgba(21, 45, 124, 0.2);
    border: 1px solid rgba(17, 96, 145, 1);
    margin: 0 0.075rem;
    > p:first-child {
      font-weight: bold;
      font-size: 0.3rem;
      margin-top: 0.1rem;
    }
    .icon {
      width: 1.03rem;
      height: 1.03rem;
      background: url("@/assets/inverter/icon.png");
      background-size: 100% 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      margin: 0 auto;
      margin-top: 0.24rem;
      margin-bottom: 0.08rem;
      cursor: pointer;
    }
    .data {
      display: flex;
      flex-wrap: wrap;
      color: #00e1ff;
      font-size: 0.24rem;
      margin-top: 0.1rem;
      p {
        width: calc(50% - 0.2rem);
        text-align: left;
        padding-left: 0.2rem;
        margin-bottom: 0.1rem;
      }
      span {
        color: #fff;
        font-size: 0.18rem;
        margin-left: 0.11rem;
      }
    }
    .state {
      background: #005ae4;
      font-size: 0.18rem;
      padding: 0.11rem 0.19rem;
      display: flex;
      align-items: center;
      margin-top: 0.01rem;
      img {
        margin-right: 0.09rem;
      }
    }
  }
}
</style>